body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.container{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  align-items: center;
  justify-content: center;
  background-color: #003e84;
}
.small-container{
  background-color: white;
  position: relative;
  width: 350px;
  height: 400px;
  border-radius: 15px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.content{
  position: relative;
  width: 275px;
  height: 300px;
  font-size: 18px;
  display: flex;
  flex-direction: column;
  align-items: left;
  padding-top: 20px;
}
.menubutton{
  width: 100px;
  height: 60px;
  color: #fff;
  border: solid 2px #005a87;
  border-radius: 3px;
  background-color: #007cba;
  padding: 5px;
  text-align: center;
  font-weight: bold;
}
.menubutton:hover{  
  border: solid 2px #005a87;
  background-color: #005a87;
}
.name{
  padding-bottom: 3px;
  font-size: 18px;
}
.buttons{
  display: flex;
  flex-direction: row;
  width: 100%;
}
.input-box{
  width: 267px;
  height: 30px;
  font-size: 18px;
  border: solid 2px;
  border-radius: 3px;
  padding-left: 7px;
}
.row{
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 6px;
}