body{
    overflow-y: scroll;
}
.page-start .small-container{
    width: 350px;
    height: 320px;
    background-color: #efefef;
    box-shadow: 0px 0px 10px 1px #6666aa;
}
.page-start .menubutton{
    width: 100%;
    font-size: 18px;
    background-color: #003e84;
    border: none;
    margin-bottom: 10px;
    border-radius: 30px;
}
.page-start .menubutton:hover{
    font-size: 19px;
    background-color: #005ea4;
}
.page-start .buttons{
    margin-top: 15px;
    width: 100%;
    display: flex;
    flex-direction: column;
}
.page-start .other-logins{
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.page-start .other-logins h6{
    margin: 0;
    font-size: 13px;
    color: #5555ff;
}
.page-start .other-logins h6:hover{
    color: #8888ff;
}
.page-start h2{
    font-size: 30px;
    color: #686a6c;
}
.page-login .menubutton{
    width: 50%;
    height: 50px;
    font-size: 16px;
    background-color: #003e84;
    border: none;
    border-radius: 30px;
    margin-inline: 2px;
}
.page-login .menubutton:hover{
    font-size: 17px;
    background-color: #005ea4;
}
.page-login .buttons{
    margin-top: 10px;
    width: 100%;
}
.page-login h2{
    font-size: 28px;
    color: #686a6c;
    margin-bottom: 40px;
}
.page-login .input-box{
    width: 255px;
    margin-bottom: 20px;
    border: none;
    border-radius: 30px;
    padding: 10px;
    outline: none;
    box-shadow: 0px 0px 5px 1px #dedede; 
}
.page-login .small-container{
    width: 350px;
    height: 400px;
    background-color: #efefef;
    box-shadow: 0px 0px 10px 1px #6666aa;
}
.page-login .pw{
    margin-bottom: 0px;
}
#login-error{
    margin-top: 10px;
    color: red;
}
#reg-error{
    margin-top: 10px;
    color: red;
}
#reg-fin{
    margin-top: 10px;
    color: red;
}
.logo{
    width: 200px;
    height: 200px;
    padding: 30px;
}
@media only screen and (max-width: 500px) {
    .page-start .small-container{
        width: 100vw;
        height: 100vh;
        border-radius: 0px;
        background-color: #efefef;
        box-shadow: none;
    }
    .page-start{
        background-color: #efefef;
    }
    .page-login .small-container{
        width: 100vw;
        height: 100vh;
        border-radius: 0px;
        background-color: #efefef;
        box-shadow: none;
    }
    .page-login{
        background-color: #efefef;
    }
    .logo{
        width: 200px;
        height: 200px;
        padding: 0px;
        margin: 30px;
        border-radius: 200px;
        box-shadow: 0px 0px 10px 1px #888899;
    }
}