body{
    overflow-y: hidden;
    overflow-x: hidden;
}
.panel-page{
    overflow-y: hidden;
    overflow-x: hidden;
    display: block;
}
.panel-page{
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: row;
}
/* Navigation */
.navbar{
    display: block;
    width: 300px;
    min-height: 100vh;
    height: max-content;
    background-color: #007cba;
}
.info{
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.cred-row{
    display: flex;
    flex-direction: row;
}
.user-credentials{
    font-size: 18px;
    font-weight: bold;
    color: white;
}
.user-role{
    font-size: 14px;
    font-style: italic;
    font-weight: bold;
    color: white;
}
.page-buttons{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 50px;
    background-color: #008cca;
    margin-bottom: 1px;
    font-weight: bolder;
    color: white;
    outline: none;
    border: none;
    font-size: 14px;
}
.page-buttons:hover{
    background-color: #009cda;
}
#list-users:disabled{
    display: none;
    font-style: italic;
}
#edit-users:disabled{
    display: none;
    font-style: italic;
}
#list-mods:disabled{
    display: none;
    font-style: italic;
}
#company-list:disabled{
    display: none;
    font-style: italic;
}
#request-vacation:disabled{
    display: none;
    font-style: italic;
}
#requests-vac:disabled{
    display: none;
    font-style: italic;
}
#archive-vac:disabled{
    display: none;
    font-style: italic;
}
#my-vac:disabled{
    display: none;
    font-style: italic;
}
#change-pass:disabled{
    display: none;
    font-style: italic;
}
#reset-pw:disabled{
    display: none;
    font-style: italic;
}
/* Content */
.sidebar{
    width: calc(100% - 300px);
    min-height: 100vh;
    overflow-y: scroll;
    overflow-x: hidden;
    background-color: #fafafa;
}
.sidebar-area{
    width: 100%;
    height: 100%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fafafa;
}
.sidebar-content{
    width: calc(100% - 150px);
    height: calc(100% - 150px);
}
.sidebar-content h2{
    margin: 0px;
}
.input-add{
    height: 30px;
    font-size: 18px;
    border: solid 2px #005a87;
    border-radius: 2px;
    margin-top: 8px;
}
.add-col{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    min-width: 300px;
    width: 50%;
}
.add-col .page-buttons{
    margin-top: 8px;
}
.hamburger{
    position: absolute;
    background-color: #007cba;
    top: 0;
    left: 0px;
    width: 30px !important;
    height: 30px !important;
    color: white;
    border: 2px solid white;
    border-radius: 3px;
    margin: 5px;
    padding-inline: 1px;
}
.hamburger:hover{
    background-color: #008cca;
}
.logout-logo{
    position: absolute;
    background-color: #dd1c1c;
    top: 0;
    left: 40px;
    width: 25px;
    height: 30px;
    padding-right: 6px;
    padding-left: 1px;
    color: white;
    border: 2px solid white;
    border-radius: 3px;
    margin: 5px;
}
.logout-logo:hover{
    background-color: #ee2c2c;
}
.MuiTypography-h6{
    font-weight: bolder !important;
    font-size: 24px !important;
}
.MuiPopover-paper {
    max-height: 300px !important;
}
#vacation-error{
    color: red;
    font-size: 14px;
}
#vacation-result{
    color: red;
    font-size: 14px;
}
#login-error{
    color: red;
    font-size: 14px;
}
#reg-error{
    color: red;
    font-size: 14px;
}
#reg-fin{
    color: red;
    font-size: 14px;
}
#change-pw{
    color: red;
    font-size: 14px;
    margin-bottom: 5px;
}
.dashboard-page h3{
    margin: 0;
}
.pdf-page .page-buttons{
    width: 793.706px;
    margin-bottom: 3px;
}
.tab-table-1{
    display: block;
}
.tab-table-2{
    display: none;
}
.tab-table-3{
    display: none;
}
.tab-table-4{
    display: none;
}
.tab-buttons{
    width: 25%;
    height: 50px;
    background-color: #007cba;
    font-weight: bolder;
    color: white;
    outline: none;
    border: none;
    font-size: 18px;
    border-radius: 0%;
}
.tab-buttons:hover{
    background-color: #009cda;
}
.tab-buttons:focus:hover{
    background-color: #009cda;
}
.tab-buttons:focus{
    background-color: #008fcd;
}
.tab-tables .MuiPaper-elevation2{
    border: none;
    border-radius: 0%;
    border-left: 2px solid #007cba;
    border-right: 2px solid #007cba;
    box-shadow: none !important;
}
.renew-button{
    width: 100%;
    height: 50px;
    background-color: #007cba;
    font-weight: bolder;
    color: white;
    outline: none;
    border: none;
    font-size: 18px;
    border-radius: 0%;
    margin-bottom: 60px;
}
.renew-button:hover{
    background-color: #009cda;
}
.input-box{
    margin-bottom: 20px;
}
.pw{
    margin-bottom: 3px;
}
.pw-button{
    width: 280px;
    height: 50px;
    background-color: #007cba;
    font-weight: bolder;
    color: white;
    outline: none;
    border: none;
    font-size: 16px;
    border-radius: 0%;
}
.pw-button:hover{
    background-color: #009cda;
}
.dash-info{
    font-size: 18px;
    padding-top: 5px;
    padding-bottom: 5px;
}
.dash-info-top{
    padding-bottom: 30px;
}
#text-area{
    min-width: 300px;
    max-width: 300px;
    min-height: 60px;
    max-height: 200px;
    font-size: 16px;
}
.Component-horizontalScrollContainer-40{
    padding-inline: 20px;
}
.Component-horizontalScrollContainer-12{
    padding-inline: 20px;
}
.Component-horizontalScrollContainer-26{
    padding-inline: 20px;
}
.dash-info-wrap{
    border-top: 2px solid #007cba;
    border-bottom: 2px solid #007cba;;
}
.request-page .page-buttons{
    width: 305px;
}
.request-page h3{
    margin-bottom: 5px;
}
.nav-misc{
    z-index: 20;
}
.vtype .MuiFormControl-root{
    margin: 0;
}
.vdate {
    width: 306px;
}
.vdate .MuiFormControl-root{
    width: calc(90px);
    margin: 0;
}
.sdate{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.edate{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
@media only screen and (max-width: 1000px) {
    .sidebar-content {
        width: calc(100% - 40px);
    }
    .content-area {
        width: calc(100% - 20px);
    }
    .tab-buttons{
        font-size: 16px;
        padding: 0;
    }
}
@media only screen and (max-width: 500px) {
    .sidebar-content {
        width: calc(100% - 20px);
    }
    .content-area {
        width: calc(100% - 20px);
    }
    .navbar{
        width: 100vw;
        position: absolute;
        border: none;
        z-index: 20;
        overflow-y: hidden;
    }
    .nav-page .page-buttons{
        width: 100vw;
        z-index: 20;
    }
    .sidebar{
        width: 100vw;
    }
    .nav-misc{
        z-index: 21;
    }
    .tab-buttons{
        font-size: 12px;
        padding: 0;
    }
    .dashboard-page .input-box{
        width: calc(100% - 14px);
    }
    .dashboard-page .buttons .pw-button{
        width: calc(100vw - 8px);
        margin: 0px;
        padding: 0px;
    }
    .request-page .page-buttons{
        width: calc(100vw - 20px);
    }
    .renew-button{
        font-size: 14px;
    }
    #text-area{
        min-width: calc(100vw - 27px);
        max-width: calc(100vw - 27px);
    }
    .MuiTableCell-head{
        font-size: 12px !important;
        line-height: 1rem !important;
        padding: 10px !important;
    }
    .MuiTableCell-root{
        font-size: 12px !important;
        line-height: 1rem !important;
        padding: 10px !important;
    }
    .MuiTableCell-footer{
        font-size: 12px !important;
        line-height: 1rem !important;
        padding: 0px !important;
        width: 100%;
    }
    .MuiTypography-root{
        font-size: 18px !important;
    }
    .MuiInputBase-root{
        transform: scale(0.9);
    }
    .reset-page .MuiFormControl-root{
        width: calc(100vw + 20px);
        margin-left: -20px !important;
    }
    .vtype .MuiFormControl-root{
        width: calc(100vw + 20px);
        margin-left: -20px !important;
    }
    .vdate{
        width: calc(100vw - 20px);
    }
    .vdate .MuiFormControl-root{
        width: calc(30vw);
        margin: 0;
    }
    .sdate{
        width: calc(100vw - 20px);
    }
    .edate{
        width: calc(100vw - 20px);
    }
}
/* User puslapio stiliai */
.page-user .menubutton{
    width: 120px;
    height: 30px;
}
.session .menubutton{
    border: solid 2px #005a87;
    background-color: #005a87;
    width: 120px;
    height: 30px;
    margin-left: 5px;
    margin-right: 5px;
}
.session .menubutton:hover{
    background-color: #006a97;
}
#tim{
    color: white;
}